.stc_card {
  border-radius: var(--radius-6);
  border: 1px solid var(--border-color-1);
  background-color: #ffffff;
  margin-bottom: 1rem;
}
.stc_card > h6,
.stc_card_herder > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #121212;
  padding-block: 1rem;
  padding-left: 1rem;
  margin: 0;
}
.stc_card_herder {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  width: 100%;
  border-bottom: 0.5px solid #d3d3d3;
  padding-right: 1rem;
}
.stc_card_herder > img {
  width: 1.5rem;
  height: 1.5rem;
  cursor: pointer;
}
.stc_container {
  padding: 0.8rem 1rem;
  border-bottom: 0.5px solid #d3d3d3;
  cursor: pointer;
  transition: all ease-in-out 200ms 50ms;
}
.stc_container:hover {
  background-color: rgba(75, 26, 133, 0.1);
}
.stc_container > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #001a72;
  margin-bottom: 0.2rem;
}
.stc_container > p {
  font-weight: 400;
  font-size: 0.878vw;
  line-height: 1.098vw;
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 0;
}
